.App {
  text-align: center;
  margin-bottom: 200px;
}

li{
  list-style: none;
}

a {
  text-decoration: none;
  color: rgb(25, 118, 210);
}

.form{
  display: flex;
  margin: 0 auto;
  margin-left: 30px;
  width: 1700px;
}

.m-1 select{
  margin: 3px;
}

.inventory{
  margin-top: 70px;
}

.inventory h2{
  display: flex;
  
}

.textarea{
  padding: 5px;
  color: rgba(0, 0, 0, 0.6);
  font-family: "Roboto","Helvetica","Arial",sans-serif;
  font-weight: 400;
  font-size: 0.75rem;
  line-height: 1.66;
  letter-spacing: 0.03333em;
  text-align: left;
  margin-top: 4px;
  margin-right: 14px;
  margin-bottom: 0;
  margin-left: 14px;
  max-width: 600px;
  width: 400px;
  max-height: 200px;
}

.row{
  display: flex;
}

.rowBtn{
  display: flex;
  width: 100%;
  justify-content: center;
  margin-bottom: 10px;
}

.printBtn{
  margin-left: 20px;
}

.applicant-item div{
  font-size: 14px;
}

.row span {
  color: rgba(0, 0, 0, 0.6);
  /* white-space: nowrap; */
  font-family: "Roboto","Helvetica","Arial",sans-serif;
  font-weight: 400;
  font-size: 0.75rem;
  line-height: 1.66;
  letter-spacing: 0.03333em;
  text-align: left;
  margin-top: 4px;
  margin-right: 3px;
  margin-bottom: 0;
  margin-left: 3px;
}

.input{
  border: 1px solid rgb(179, 179, 179);
  border-radius: 4px;
  color: rgba(0, 0, 0, 0.87);
  height: 30px;
  padding: 0px 10px;
  font: inherit;
  margin: 3px;
  margin-bottom: 10px;
  font-size: 0.75rem;
}

select{
  border: 1px solid rgb(179, 179, 179);
  border-radius: 4px;
  color: rgba(0, 0, 0, 0.87);
  height: 30px;
  padding: 0px 10px;
  font: inherit;
  margin: 3px;
  margin-bottom: 10px;
  font-size: 0.75rem;
}

.column{
  display: flex;
  flex-direction: column;
}

.column span{
  margin: 0;
}

.small{
  width: 100px;
}

.big{
  width: 310px;
}

.large{
  width: 120px;
}

.full{
  width: 500px;
}

.input_wrapper{
  display: flex;
  margin: 10px;
  width: 300px;
}

.main{
  width: 100%;
  margin: 0 auto;
}

.list-spec{
  display: flex;
}

.list-spec ul{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 50%;
  margin: 40px auto;
  padding: 10px;
}

.ul-right li{
  border-left: none !important;
}

.ul-left{
  width: 100px !important;
}

.list-spec ul li{
  border: 1px solid;
  border-bottom: none;
  width: 100%;
  padding: 10px;
  cursor: pointer;
}

.list-spec ul li:hover{
  background-color: rgb(218, 218, 218);
}

.row-li{
  width: 100%;
}

.row-li li{
  display: flex;
  justify-content: flex-start;
}

.row-li li span{
 padding-left: 10px;
 text-align: left;
}

.create-spec{
  width: 80%;
  margin: 0 auto;
}

.center{
  justify-content: center;
}

.btn{
 margin-left: 20px;
}

.btn-group{
  margin-top: 20px;
}


.applicant-list{
  display: flex;
  flex-direction: column;
  width: 90%;
  margin: 0 auto;
}

.applicant-item{
  display: flex;
  justify-content: space-between;
  padding: 10px;
  border: 1px solid;
  border-radius: 4px;
  margin: 3px 0;
  
}

.select-status select{
  cursor: pointer;
  width: 102px;
}

.await{
  background-color: rgb(193, 250, 250) !important;
}

.false{
  background-color: rgb(250, 166, 166) !important;
}

.true{
  background-color: rgb(176, 255, 176) !important;
}
.applicant-header{
  display: flex;
  justify-content: space-between;
  padding: 10px;
  border: 1px solid;
  border-radius: 4px;
  margin: 3px 0;
}

.applicant-header div{
  font-size: 12px;
  font-weight: 700;
}

.w5{
  width: 5%;
}

.w10{
  width: 10%;
}

.w20{
  width: 20%;
}

.w25{
  width: 25%;
}

.w30{
  width: 30%;
}

.red{
  color: rgb(255, 212, 212);
}

.spec{
  max-width: 500px;
}

.small-main{
  width: 50%;
}

html{
  background: #f5f5dc;
}


@media print {
  /* здесь будут стили для печати */
  .applicant-item{
    display: flex;
    justify-content: space-between;
    padding: 10px;
    border: 1px solid;
    border-radius: 0 !important;
    margin: 0 !important;
  }
  .select-status select{
    border: none;
    border-radius: 0;
    color: rgba(0, 0, 0, 0.87);
    width: 90px !important;
    height: 30px;
    padding: 0px 10px;
    font: inherit;
    margin: 3px;
    margin-bottom: 10px;
    font-size: 0.75rem;
  }

  .fio{
    width: 200px !important;
    font-size: 10px !important;
  }
  .fio-header{
    width: 200px !important;
    font-size: 10px !important;
  }
  .row{
    font-size: 10px !important;
  }
  .row span{
    font-size: 10px !important;
  }
  .tell{
    width: 130px !important;
  }
}

.mainmain{
  display: flex;
  width: 100px;
  margin: 0 auto;
  flex-direction: column;
  align-items: center;
}

h2{
  white-space: nowrap;
}

.mainmain table {
  width: 100%;
}

.full-width{
  width: 100%;
}

th, td{
  border: 1px solid;
  padding: 10px;
}
table{
  border-collapse: collapse;
}